.TitleHeader {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  &__icon {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding-right: $size-m;
    img {
      width: 100%;
    }
  }
  &__title {
    display: flex;
    flex-direction: column;
    &__heading {
      text-align: initial;
      font-weight: $font-bold;
      font-size: $font-l;
      line-height: 28px;
      color: $text;
    }
    &__text {
      color: $grey-text;
      line-height: 2.4rem;
    }
  }
}
