.card {
  display: flex;
  background: $white;
  position: absolute;
  flex-direction: column;
  transform: translate(-50%, -0%);
  left: 50%;
  max-height: 90vh;
  overflow: hidden;
  width: 50vw;
  border-radius: $radius-s;
  padding: 3.5rem 4rem;
  @include respond(ph) {
    width: 100%;
    background: red;
  }
  &__header {
    display: flex;
  }
  &__fields {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 3;
  }
}
