.amount {
  display: flex;
  justify-content: center;
  font-size: $font-xs;
  align-items: center;
  margin-top: 4em;

  &__field {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
    &__label {
      display: flex;
      text-transform: capitalize;
      font-size: $size-s;
      margin-bottom: 0.5rem;
      font-weight: $font-regular;
      letter-spacing: 0.1rem;
      color: $text;
    }
    &__input {
      width: 100%;
      font-size: $font-l;
      padding: 2rem 4.5rem;
      height: 5.6rem;
      border-radius: $radius-s;
      border: 1px solid $border-color;
      text-align: left;
      font-weight: $font-bold;
      color: $grey-eleven;
    }

    &__input:focus {
      outline: none;
      color: $grey-eleven;
      // box-shadow: 0 8px 6px -6px $grey-shadow;
      // transition: box-shadow 0.2s ease !important;
      // vertical-align: middle !important;
    }
  }
  &__field:before {
    content: '$';
    display: flex;
    position: fixed;
    top: 17.1rem;
    left: 6rem;
    z-index: 1;
    font-size: $font-l;
    color: $grey-seven;
  }
}
